import Vue from 'vue';
import Vuex from 'vuex';

import { getMusicInfo } from '@/service/api';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    /* 歌曲列表 */
    musicList: [{
      id: 0, // 歌曲id
      musicName: '', // 歌曲名字
      musicSinger: '', // 歌手名字
      musicImg: '', // 歌曲图片
      musicUrl: '', // 歌曲地址
    }],
    /* 音乐是否在播放 */
    musicStatus: false,
    // 当前播放的音乐
    musicIndex: 0,
    // 管理音乐
    music: {
      play: Function,
      pause: Function,
    },
  },
  mutations: {
    setMusicList(state, musicList) {
      state.musicList = musicList;
    },
    setMusic(state, music) {
      state.music = music;
    },
    // 播放暂停
    playMusic(state) {
      state.musicStatus = !state.musicStatus;
      if (state.musicStatus) {
        state.music.play();
      } else {
        state.music.pause();
      }
    },
    // 上一首
    beforeMusic(state) {
      if (state.musicIndex <= 0) {
        state.musicIndex = state.musicList.length - 1;
        return;
      }
      state.musicIndex -= 1;
      if (state.musicStatus) {
        setTimeout(() => {
          state.music.play();
        }, 1000);
      }
    },
    // 下一首
    lastMusic(state) {
      if (state.musicIndex >= state.musicList.length - 1) {
        state.musicIndex = 0;
        return;
      }
      state.musicIndex += 1;
      if (state.musicStatus) {
        setTimeout(() => {
          state.music.play();
        }, 1000);
      }
    },
    // 选择播放
    chooseMusic(state, musicIndex) {
      state.musicIndex = musicIndex;
      if (state.musicStatus) {
        setTimeout(() => {
          state.music.play();
        }, 1000);
      }
    },
  },
  actions: {
    getMusicList({commit}) {
      getMusicInfo()
        .then((response) => {
          commit('setMusicList', response.data);
        })
        .catch((error) => {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    },
  },
  modules: {},
});
