<template>
  <div>
    <router-view/>
    <audio ref="music" id="music" :src="musicList[musicIndex].musicUrl"/>
  </div>
</template>

<script>

export default {
  name: 'MusicPlayer',
  mounted() {
    this.$store.dispatch('getMusicList');
    this.$store.commit('setMusic', this.$refs.music);
  },
  computed: {
    musicPlay() {
      return this.$store.state.musicStatus;
    },
    musicList() {
      return this.$store.state.musicList;
    },
    musicIndex() {
      return this.$store.state.musicIndex;
    },
  },
};
</script>

